.profile_article__title {
  text-align: center;
  justify-content: center;
  border-bottom: 1px solid #000; 
  line-height: 0.1em;
  margin: 2.5rem 0px 2rem;
  font-size: 2.5rem;
  color: rgb(131, 131, 187);
  margin-left: 2rem;
  margin-right: 2rem;
}

.profile_article__title span {
  background-color: #eeeeee;
  padding:0 10px;
}

.profile_article__title img {
  padding:0 10px; 
  text-align: center;
  justify-content: center;
  font-size: 2.5rem;
}